'use client';

import { motion, AnimatePresence } from 'framer-motion';
import Button from '@/components/ui/Button';
import Confetti from './Confetti';
import styles from './LevelUpModal.module.css';

interface LevelUpModalProps {
    show: boolean;
    level: number;
    onClose: () => void;
}

const LevelUpModal = ({ show, level, onClose }: LevelUpModalProps) => {
    return (
        <AnimatePresence>
            {show && (
                <>
                    <Confetti show={show} duration={4000} />
                    <motion.div
                        className={styles.overlay}
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        onClick={onClose}
                    >
                        <motion.div
                            className={styles.modal}
                            initial={{ scale: 0.5, opacity: 0, y: 100 }}
                            animate={{ scale: 1, opacity: 1, y: 0 }}
                            exit={{ scale: 0.5, opacity: 0, y: 100 }}
                            transition={{ type: 'spring', stiffness: 400, damping: 25 }}
                            onClick={(e) => e.stopPropagation()}
                        >
                            <div className={styles.glow} />
                            <motion.div
                                className={styles.star}
                                animate={{ rotate: 360 }}
                                transition={{ duration: 20, repeat: Infinity, ease: 'linear' }}
                            >
                                â­
                            </motion.div>
                            <motion.div
                                className={styles.levelBadge}
                                initial={{ scale: 0 }}
                                animate={{ scale: 1 }}
                                transition={{ type: 'spring', stiffness: 500, damping: 20, delay: 0.3 }}
                            >
                                <span className={styles.levelNumber}>{level}</span>
                            </motion.div>
                            <motion.h2
                                className={styles.title}
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                transition={{ delay: 0.4 }}
                            >
                                Level Up!
                            </motion.h2>
                            <motion.p
                                className={styles.subtitle}
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                transition={{ delay: 0.5 }}
                            >
                                You've reached <span className={styles.highlight}>Level {level}</span>
                            </motion.p>
                            <motion.p
                                className={styles.message}
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                transition={{ delay: 0.6 }}
                            >
                                Keep exploring and unlocking opportunities!
                            </motion.p>
                            <motion.div
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                transition={{ delay: 0.7 }}
                            >
                                <Button variant="glow" size="lg" onClick={onClose}>
                                    Continue
                                </Button>
                            </motion.div>
                        </motion.div>
                    </motion.div>
                </>
            )}
        </AnimatePresence>
    );
};

export default LevelUpModal;
