'use client';

import { useEffect, useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import styles from './ProgressBar.module.css';

interface ProgressBarProps {
    value: number;
    max: number;
    label?: string;
    showValue?: boolean;
    variant?: 'default' | 'xp' | 'streak' | 'goal';
    size?: 'sm' | 'md' | 'lg';
    animated?: boolean;
    onComplete?: () => void;
}

const ProgressBar = ({
    value,
    max,
    label,
    showValue = true,
    variant = 'default',
    size = 'md',
    animated = true,
    onComplete,
}: ProgressBarProps) => {
    const [displayValue, setDisplayValue] = useState(animated ? 0 : value);
    const percentage = Math.min((displayValue / max) * 100, 100);
    const isComplete = displayValue >= max;

    useEffect(() => {
        if (animated) {
            const timer = setTimeout(() => {
                setDisplayValue(value);
                if (value >= max && onComplete) {
                    onComplete();
                }
            }, 100);
            return () => clearTimeout(timer);
        } else {
            setDisplayValue(value);
        }
    }, [value, max, animated, onComplete]);

    return (
        <div className={`${styles.container} ${styles[size]}`}>
            {(label || showValue) && (
                <div className={styles.header}>
                    {label && <span className={styles.label}>{label}</span>}
                    {showValue && (
                        <span className={styles.value}>
                            {Math.round(displayValue)} / {max}
                        </span>
                    )}
                </div>
            )}
            <div className={`${styles.track} ${styles[variant]}`}>
                <motion.div
                    className={`${styles.fill} ${styles[`fill-${variant}`]}`}
                    initial={{ width: 0 }}
                    animate={{ width: `${percentage}%` }}
                    transition={{ duration: 0.8, ease: 'easeOut' }}
                />
                {variant === 'xp' && (
                    <div className={styles.shimmer} style={{ width: `${percentage}%` }} />
                )}
                {isComplete && variant === 'goal' && (
                    <motion.div
                        className={styles.completeIcon}
                        initial={{ scale: 0, opacity: 0 }}
                        animate={{ scale: 1, opacity: 1 }}
                        transition={{ type: 'spring', stiffness: 500, damping: 25, delay: 0.5 }}
                    >
                        âœ“
                    </motion.div>
                )}
            </div>
        </div>
    );
};

export default ProgressBar;

// XP Bar Component (for header)
interface XPBarProps {
    currentXP: number;
    xpToNextLevel: number;
    level: number;
    compact?: boolean;
}

export const XPBar = ({ currentXP, xpToNextLevel, level, compact = false }: XPBarProps) => {
    const xpInCurrentLevel = currentXP % xpToNextLevel;
    const percentage = (xpInCurrentLevel / xpToNextLevel) * 100;

    return (
        <div className={`${styles.xpBar} ${compact ? styles.xpBarCompact : ''}`}>
            <div className={styles.xpLevel}>
                <span className={styles.xpLevelNumber}>{level}</span>
            </div>
            <div className={styles.xpTrack}>
                <motion.div
                    className={styles.xpFill}
                    style={{ width: `${percentage}%` }}
                    layoutId="xp-fill"
                />
                <div className={styles.xpShimmer} style={{ width: `${percentage}%` }} />
            </div>
            {!compact && (
                <span className={styles.xpText}>
                    {xpInCurrentLevel} / {xpToNextLevel} XP
                </span>
            )}
            <div className={styles.xpLevel}>
                <span className={styles.xpLevelNumber}>{level + 1}</span>
            </div>
        </div>
    );
};

// XP Gain Animation
interface XPGainProps {
    amount: number;
    show: boolean;
    onComplete: () => void;
}

export const XPGain = ({ amount, show, onComplete }: XPGainProps) => (
    <AnimatePresence>
        {show && (
            <motion.div
                className={styles.xpGain}
                initial={{ opacity: 1, y: 0, scale: 1 }}
                animate={{ opacity: 0, y: -40, scale: 1.3 }}
                exit={{ opacity: 0 }}
                transition={{ duration: 1.2, ease: 'easeOut' }}
                onAnimationComplete={onComplete}
            >
                +{amount} XP
            </motion.div>
        )}
    </AnimatePresence>
);
