'use client';

import { motion } from 'framer-motion';
import {
    Trophy, Award, Brain, Wrench,
    Users, Briefcase, ChevronRight
} from 'lucide-react';
import styles from './CategoryExplorer.module.css';

interface Category {
    id: string;
    name: string;
    icon: React.ElementType;
    color: string;
    count: number;
}

const categories: Category[] = [
    {
        id: 'competitions',
        name: 'Competitions',
        icon: Trophy,
        color: '#f97316',
        count: 85,
    },
    {
        id: 'scholarships',
        name: 'Scholarships',
        icon: Award,
        color: '#a855f7',
        count: 120,
    },
    {
        id: 'olympiads',
        name: 'Olympiads',
        icon: Brain,
        color: '#3b82f6',
        count: 45,
    },
    {
        id: 'hackathons',
        name: 'Hackathons',
        icon: Wrench,
        color: '#22c55e',
        count: 35,
    },
    {
        id: 'communities',
        name: 'Communities',
        icon: Users,
        color: '#ec4899',
        count: 28,
    },
    {
        id: 'internships',
        name: 'Internships',
        icon: Briefcase,
        color: '#06b6d4',
        count: 60,
    },
];

interface CategoryExplorerProps {
    onCategoryClick: (categoryId: string) => void;
}

const CategoryExplorer = ({ onCategoryClick }: CategoryExplorerProps) => {
    const containerVariants = {
        hidden: { opacity: 0 },
        visible: {
            opacity: 1,
            transition: { staggerChildren: 0.08 },
        },
    };

    const itemVariants = {
        hidden: { opacity: 0, y: 20 },
        visible: {
            opacity: 1,
            y: 0,
            transition: { type: 'spring', stiffness: 400, damping: 25 }
        },
    };

    return (
        <section className={styles.section}>
            <div className={styles.header}>
                <motion.h2
                    className={styles.title}
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    viewport={{ once: true }}
                >
                    What's Your <span className={styles.highlight}>Vibe</span>?
                </motion.h2>
                <motion.p
                    className={styles.subtitle}
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    viewport={{ once: true }}
                    transition={{ delay: 0.1 }}
                >
                    Pick a category you can't stop thinking about and we'll show opportunities perfect for you!
                </motion.p>
                <motion.a
                    href="/categories"
                    className={styles.viewAll}
                    initial={{ opacity: 0 }}
                    whileInView={{ opacity: 1 }}
                    viewport={{ once: true }}
                    transition={{ delay: 0.2 }}
                >
                    View all categories <ChevronRight size={16} />
                </motion.a>
            </div>

            <motion.div
                className={styles.grid}
                variants={containerVariants}
                initial="hidden"
                whileInView="visible"
                viewport={{ once: true, margin: '-100px' }}
            >
                {categories.map((category) => (
                    <motion.button
                        key={category.id}
                        className={styles.card}
                        variants={itemVariants}
                        whileHover={{ y: -6 }}
                        whileTap={{ scale: 0.98 }}
                        onClick={() => onCategoryClick(category.id)}
                    >
                        <div
                            className={styles.iconContainer}
                            style={{ backgroundColor: `${category.color}20`, color: category.color }}
                        >
                            <category.icon size={24} />
                        </div>
                        <div className={styles.cardContent}>
                            <h3 className={styles.cardTitle}>{category.name}</h3>
                            <span className={styles.cardCount}>{category.count} opportunities</span>
                        </div>
                    </motion.button>
                ))}
            </motion.div>
        </section>
    );
};

export default CategoryExplorer;
