'use client';

import { useEffect, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import styles from './Confetti.module.css';

interface ConfettiProps {
    show: boolean;
    onComplete?: () => void;
    duration?: number;
}

const COLORS = ['#8b5cf6', '#ec4899', '#06b6d4', '#84cc16', '#f59e0b', '#ef4444'];
const PARTICLE_COUNT = 100;

interface Particle {
    id: number;
    x: number;
    color: string;
    delay: number;
    duration: number;
    rotation: number;
    size: number;
    type: 'circle' | 'square' | 'star';
}

const generateParticles = (): Particle[] => {
    return Array.from({ length: PARTICLE_COUNT }, (_, i) => ({
        id: i,
        x: Math.random() * 100,
        color: COLORS[Math.floor(Math.random() * COLORS.length)],
        delay: Math.random() * 0.5,
        duration: 2 + Math.random() * 2,
        rotation: Math.random() * 720,
        size: 6 + Math.random() * 6,
        type: ['circle', 'square', 'star'][Math.floor(Math.random() * 3)] as Particle['type'],
    }));
};

const Confetti = ({ show, onComplete, duration = 3000 }: ConfettiProps) => {
    const particles = useRef<Particle[]>(generateParticles());

    useEffect(() => {
        if (show && onComplete) {
            const timer = setTimeout(onComplete, duration);
            return () => clearTimeout(timer);
        }
    }, [show, duration, onComplete]);

    return (
        <AnimatePresence>
            {show && (
                <motion.div
                    className={styles.container}
                    initial={{ opacity: 1 }}
                    exit={{ opacity: 0 }}
                    transition={{ duration: 0.5, delay: duration / 1000 - 0.5 }}
                >
                    {particles.current.map((particle) => (
                        <motion.div
                            key={particle.id}
                            className={styles.particle}
                            style={{
                                left: `${particle.x}%`,
                                width: particle.size,
                                height: particle.size,
                                backgroundColor: particle.type !== 'star' ? particle.color : 'transparent',
                                borderRadius: particle.type === 'circle' ? '50%' : 0,
                            }}
                            initial={{ y: -20, opacity: 1, rotate: 0 }}
                            animate={{
                                y: '100vh',
                                opacity: [1, 1, 0],
                                rotate: particle.rotation,
                            }}
                            transition={{
                                duration: particle.duration,
                                delay: particle.delay,
                                ease: 'easeIn',
                            }}
                        >
                            {particle.type === 'star' && (
                                <span style={{ color: particle.color, fontSize: particle.size * 1.5 }}>â˜…</span>
                            )}
                        </motion.div>
                    ))}
                </motion.div>
            )}
        </AnimatePresence>
    );
};

export default Confetti;

// Celebration Component (Confetti + Message)
interface CelebrationProps {
    show: boolean;
    title?: string;
    message?: string;
    emoji?: string;
    onClose?: () => void;
}

export const Celebration = ({
    show,
    title = 'Amazing!',
    message = "You're doing great!",
    emoji = 'ðŸŽ‰',
    onClose,
}: CelebrationProps) => (
    <AnimatePresence>
        {show && (
            <>
                <Confetti show={show} onComplete={onClose} />
                <motion.div
                    className={styles.celebrationOverlay}
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    exit={{ opacity: 0 }}
                >
                    <motion.div
                        className={styles.celebrationCard}
                        initial={{ scale: 0.5, opacity: 0, y: 50 }}
                        animate={{ scale: 1, opacity: 1, y: 0 }}
                        exit={{ scale: 0.5, opacity: 0, y: 50 }}
                        transition={{ type: 'spring', stiffness: 400, damping: 25 }}
                    >
                        <motion.span
                            className={styles.celebrationEmoji}
                            animate={{ rotate: [0, -15, 15, -10, 10, 0] }}
                            transition={{ duration: 0.5, delay: 0.3 }}
                        >
                            {emoji}
                        </motion.span>
                        <h2 className={styles.celebrationTitle}>{title}</h2>
                        <p className={styles.celebrationMessage}>{message}</p>
                    </motion.div>
                </motion.div>
            </>
        )}
    </AnimatePresence>
);
