'use client';

import { useState, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ChevronLeft, ChevronRight } from 'lucide-react';
import OpportunityCard, { type Opportunity } from './OpportunityCard';
import styles from './OpportunityCarousel.module.css';

// Sample opportunities data
const sampleOpportunities: Opportunity[] = [
    {
        id: '1',
        title: 'National Science Olympiad 2024',
        category: 'Olympiad',
        categoryColor: '#f59e0b',
        deadline: new Date(Date.now() + 3 * 24 * 60 * 60 * 1000),
        difficulty: 'Intermediate',
        applicants: 2500,
        rating: 4.8,
        tags: ['Science', 'Physics', 'Chemistry'],
        isHot: true,
    },
    {
        id: '2',
        title: 'INSPIRE Scholarship for Higher Education',
        category: 'Scholarship',
        categoryColor: '#8b5cf6',
        deadline: new Date(Date.now() + 14 * 24 * 60 * 60 * 1000),
        difficulty: 'Beginner',
        applicants: 5000,
        rating: 4.9,
        tags: ['Merit-based', 'STEM', 'Government'],
        isNew: true,
    },
    {
        id: '3',
        title: 'International Math Olympiad Qualifier',
        category: 'Olympiad',
        categoryColor: '#f59e0b',
        deadline: new Date(Date.now() + 21 * 24 * 60 * 60 * 1000),
        difficulty: 'Advanced',
        applicants: 1200,
        rating: 4.7,
        tags: ['Mathematics', 'Problem Solving'],
    },
    {
        id: '4',
        title: 'Google Code-in Competition',
        category: 'Competition',
        categoryColor: '#ec4899',
        deadline: new Date(Date.now() + 7 * 24 * 60 * 60 * 1000),
        difficulty: 'Intermediate',
        applicants: 3500,
        rating: 4.6,
        tags: ['Coding', 'Open Source'],
        isHot: true,
    },
    {
        id: '5',
        title: 'Young Innovators Science Fair',
        category: 'Competition',
        categoryColor: '#ec4899',
        deadline: new Date(Date.now() + 30 * 24 * 60 * 60 * 1000),
        difficulty: 'Beginner',
        applicants: 800,
        rating: 4.5,
        tags: ['Innovation', 'Projects'],
        isNew: true,
    },
    {
        id: '6',
        title: 'AWS Cloud Skills Workshop',
        category: 'Workshop',
        categoryColor: '#06b6d4',
        deadline: new Date(Date.now() + 5 * 24 * 60 * 60 * 1000),
        difficulty: 'Beginner',
        applicants: 450,
        rating: 4.8,
        tags: ['Cloud', 'AWS', 'Technology'],
    },
];

interface OpportunityCarouselProps {
    title?: string;
    subtitle?: string;
    opportunities?: Opportunity[];
    onOpportunityClick?: (id: string) => void;
    onSave?: (id: string) => void;
    savedIds?: string[];
}

const OpportunityCarousel = ({
    title = 'Trending Opportunities',
    subtitle = 'Don\'t miss out on these popular picks',
    opportunities = sampleOpportunities,
    onOpportunityClick,
    onSave,
    savedIds = [],
}: OpportunityCarouselProps) => {
    const [currentIndex, setCurrentIndex] = useState(0);
    const containerRef = useRef<HTMLDivElement>(null);

    const scrollToIndex = (index: number) => {
        if (containerRef.current) {
            const children = containerRef.current.children;
            if (children[index]) {
                (children[index] as HTMLElement).scrollIntoView({
                    behavior: 'smooth',
                    block: 'nearest',
                    inline: 'start',
                });
            }
        }
        setCurrentIndex(index);
    };

    const goNext = () => {
        const nextIndex = Math.min(currentIndex + 1, opportunities.length - 1);
        scrollToIndex(nextIndex);
    };

    const goPrev = () => {
        const prevIndex = Math.max(currentIndex - 1, 0);
        scrollToIndex(prevIndex);
    };

    return (
        <section className={styles.section}>
            <div className={styles.header}>
                <div className={styles.headerText}>
                    <motion.h2
                        className={styles.title}
                        initial={{ opacity: 0, y: 20 }}
                        whileInView={{ opacity: 1, y: 0 }}
                        viewport={{ once: true }}
                    >
                        {title}
                    </motion.h2>
                    <motion.p
                        className={styles.subtitle}
                        initial={{ opacity: 0, y: 20 }}
                        whileInView={{ opacity: 1, y: 0 }}
                        viewport={{ once: true }}
                        transition={{ delay: 0.1 }}
                    >
                        {subtitle}
                    </motion.p>
                </div>
                <div className={styles.controls}>
                    <motion.button
                        className={styles.navButton}
                        onClick={goPrev}
                        disabled={currentIndex === 0}
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                    >
                        <ChevronLeft size={20} />
                    </motion.button>
                    <motion.button
                        className={styles.navButton}
                        onClick={goNext}
                        disabled={currentIndex >= opportunities.length - 3}
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                    >
                        <ChevronRight size={20} />
                    </motion.button>
                </div>
            </div>

            <div className={styles.carouselContainer}>
                <motion.div
                    ref={containerRef}
                    className={styles.carousel}
                    initial={{ opacity: 0 }}
                    whileInView={{ opacity: 1 }}
                    viewport={{ once: true }}
                >
                    {opportunities.map((opportunity, index) => (
                        <motion.div
                            key={opportunity.id}
                            className={styles.cardWrapper}
                            initial={{ opacity: 0, y: 20 }}
                            whileInView={{ opacity: 1, y: 0 }}
                            viewport={{ once: true }}
                            transition={{ delay: index * 0.1 }}
                        >
                            <OpportunityCard
                                opportunity={{
                                    ...opportunity,
                                    isSaved: savedIds.includes(opportunity.id),
                                }}
                                variant={index === 0 ? 'featured' : 'default'}
                                onClick={onOpportunityClick}
                                onSave={onSave}
                            />
                        </motion.div>
                    ))}
                </motion.div>
            </div>

            {/* Progress Dots */}
            <div className={styles.dots}>
                {opportunities.slice(0, opportunities.length - 2).map((_, index) => (
                    <button
                        key={index}
                        className={`${styles.dot} ${index === currentIndex ? styles.activeDot : ''}`}
                        onClick={() => scrollToIndex(index)}
                    />
                ))}
            </div>
        </section>
    );
};

export default OpportunityCarousel;
