'use client';

import { create } from 'zustand';
import { persist } from 'zustand/middleware';

// Types
export interface Badge {
    id: string;
    name: string;
    description: string;
    icon: string;
    unlockedAt?: Date;
    category: 'exploration' | 'achievement' | 'streak' | 'milestone';
}

export interface UserProfile {
    name: string;
    grade: number;
    interests: string[];
    goals: string[];
    avatarUrl?: string;
}

export interface GameState {
    // User Profile
    profile: UserProfile | null;
    isOnboarded: boolean;

    // XP & Leveling
    xp: number;
    level: number;
    xpToNextLevel: number;

    // Streaks
    currentStreak: number;
    longestStreak: number;
    lastCheckIn: string | null;

    // Badges
    badges: Badge[];
    unlockedBadges: string[];

    // Saved Opportunities
    savedOpportunities: string[];
    appliedOpportunities: string[];
    completedOpportunities: string[];

    // UI State
    showLevelUpModal: boolean;
    showAchievementToast: boolean;
    latestAchievement: Badge | null;
    showConfetti: boolean;

    // Actions
    setProfile: (profile: UserProfile) => void;
    completeOnboarding: () => void;
    addXP: (amount: number) => void;
    checkIn: () => void;
    unlockBadge: (badgeId: string) => void;
    saveOpportunity: (id: string) => void;
    unsaveOpportunity: (id: string) => void;
    applyToOpportunity: (id: string) => void;
    completeOpportunity: (id: string) => void;
    closeLevelUpModal: () => void;
    closeAchievementToast: () => void;
    hideConfetti: () => void;
}

// XP required for each level (exponential growth)
const calculateXPForLevel = (level: number): number => {
    return Math.floor(100 * Math.pow(1.5, level - 1));
};

// Calculate level from total XP
const calculateLevelFromXP = (totalXP: number): { level: number; xpInCurrentLevel: number; xpToNextLevel: number } => {
    let level = 1;
    let xpRemaining = totalXP;

    while (xpRemaining >= calculateXPForLevel(level)) {
        xpRemaining -= calculateXPForLevel(level);
        level++;
    }

    return {
        level,
        xpInCurrentLevel: xpRemaining,
        xpToNextLevel: calculateXPForLevel(level),
    };
};

