'use client';

import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ArrowRight, ArrowLeft, Sparkles, X } from 'lucide-react';
import Button from '@/components/ui/Button';
import { useGameStore } from '@/store/gameStore';
import styles from './OnboardingModal.module.css';

interface OnboardingModalProps {
    isOpen: boolean;
    onClose: () => void;
}

const grades = [
    { value: 4, label: 'Class 4', emoji: 'ðŸŒ±' },
    { value: 5, label: 'Class 5', emoji: 'ðŸŒ¿' },
    { value: 6, label: 'Class 6', emoji: 'ðŸŒ³' },
    { value: 7, label: 'Class 7', emoji: 'ðŸš€' },
    { value: 8, label: 'Class 8', emoji: 'â­' },
    { value: 9, label: 'Class 9', emoji: 'ðŸ’«' },
    { value: 10, label: 'Class 10', emoji: 'ðŸŽ¯' },
    { value: 11, label: 'Class 11', emoji: 'ðŸ”¥' },
    { value: 12, label: 'Class 12', emoji: 'ðŸ‘‘' },
];

const interests = [
    { id: 'science', label: 'Science', emoji: 'ðŸ”¬', color: '#3b82f6' },
    { id: 'math', label: 'Mathematics', emoji: 'ðŸ“', color: '#8b5cf6' },
    { id: 'coding', label: 'Coding', emoji: 'ðŸ’»', color: '#10b981' },
    { id: 'arts', label: 'Arts', emoji: 'ðŸŽ¨', color: '#ec4899' },
    { id: 'sports', label: 'Sports', emoji: 'âš½', color: '#f59e0b' },
    { id: 'music', label: 'Music', emoji: 'ðŸŽµ', color: '#06b6d4' },
    { id: 'writing', label: 'Writing', emoji: 'âœï¸', color: '#f43f5e' },
    { id: 'business', label: 'Business', emoji: 'ðŸ’¼', color: '#84cc16' },
    { id: 'environment', label: 'Environment', emoji: 'ðŸŒ', color: '#14b8a6' },
];

const goals = [
    { id: 'scholarships', label: 'Win Scholarships', emoji: 'ðŸŽ“', description: 'Fund your education' },
    { id: 'competitions', label: 'Win Competitions', emoji: 'ðŸ†', description: 'Prove your skills' },
    { id: 'skills', label: 'Learn New Skills', emoji: 'ðŸ“š', description: 'Grow your abilities' },
    { id: 'career', label: 'Explore Careers', emoji: 'ðŸš€', description: 'Find your path' },
];

const OnboardingModal = ({ isOpen, onClose }: OnboardingModalProps) => {
    const [step, setStep] = useState(0);
    const [name, setName] = useState('');
    const [selectedGrade, setSelectedGrade] = useState<number | null>(null);
    const [selectedInterests, setSelectedInterests] = useState<string[]>([]);
    const [selectedGoals, setSelectedGoals] = useState<string[]>([]);

    const { setProfile, completeOnboarding } = useGameStore();

    const toggleInterest = (id: string) => {
        setSelectedInterests((prev) =>
            prev.includes(id) ? prev.filter((i) => i !== id) : [...prev, id]
        );
    };

    const toggleGoal = (id: string) => {
        setSelectedGoals((prev) =>
            prev.includes(id) ? prev.filter((g) => g !== id) : [...prev, id]
        );
    };

    const canProceed = () => {
        switch (step) {
            case 0: return true; // Welcome
            case 1: return name.trim().length >= 2;
            case 2: return selectedGrade !== null;
            case 3: return selectedInterests.length >= 2;
            case 4: return selectedGoals.length >= 1;
            default: return false;
        }
    };

    const handleNext = () => {
        if (step < 4) {
            setStep(step + 1);
        } else {
            // Complete onboarding
            setProfile({
                name,
                grade: selectedGrade!,
                interests: selectedInterests,
                goals: selectedGoals,
            });
            completeOnboarding();
            onClose();
        }
    };

    const handleBack = () => {
        if (step > 0) {
            setStep(step - 1);
        }
    };

    const steps = [
        // Step 0: Welcome
        <motion.div
            key="welcome"
            className={styles.stepContent}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
        >
            <motion.div
                className={styles.welcomeEmoji}
                animate={{ scale: [1, 1.1, 1], rotate: [0, 5, -5, 0] }}
                transition={{ repeat: Infinity, duration: 2 }}
            >
                ðŸŽ®
            </motion.div>
            <h2 className={styles.stepTitle}>Welcome to Myark!</h2>
            <p className={styles.stepDescription}>
                Ready to discover amazing opportunities and level up your future?
                Let's make this journey fun! ðŸš€
            </p>
            <div className={styles.xpPreview}>
                <Sparkles size={16} />
                <span>Complete setup to earn <strong>+75 XP</strong></span>
            </div>
        </motion.div>,

        // Step 1: Name
        <motion.div
            key="name"
            className={styles.stepContent}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
        >
            <span className={styles.stepEmoji}>ðŸ‘‹</span>
            <h2 className={styles.stepTitle}>What's your name?</h2>
            <p className={styles.stepDescription}>
                We'll use this to personalize your experience
            </p>
            <input
                type="text"
                className={styles.nameInput}
                placeholder="Enter your name..."
                value={name}
                onChange={(e) => setName(e.target.value)}
                autoFocus
            />
        </motion.div>,

        // Step 2: Grade
        <motion.div
            key="grade"
            className={styles.stepContent}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
        >
            <span className={styles.stepEmoji}>ðŸŽ’</span>
            <h2 className={styles.stepTitle}>Which class are you in?</h2>
            <p className={styles.stepDescription}>
                We'll show you opportunities perfect for your level
            </p>
            <div className={styles.gradeGrid}>
                {grades.map((grade) => (
                    <motion.button
                        key={grade.value}
                        className={`${styles.gradeButton} ${selectedGrade === grade.value ? styles.selected : ''
                            }`}
                        onClick={() => setSelectedGrade(grade.value)}
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                    >
                        <span className={styles.gradeEmoji}>{grade.emoji}</span>
                        <span className={styles.gradeLabel}>{grade.label}</span>
                    </motion.button>
                ))}
            </div>
        </motion.div>,

        // Step 3: Interests
        <motion.div
            key="interests"
            className={styles.stepContent}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
        >
            <span className={styles.stepEmoji}>ðŸ’¡</span>
            <h2 className={styles.stepTitle}>What interests you?</h2>
            <p className={styles.stepDescription}>
                Pick at least 2 to unlock personalized recommendations
            </p>
            <div className={styles.interestsGrid}>
                {interests.map((interest) => (
                    <motion.button
                        key={interest.id}
                        className={`${styles.interestButton} ${selectedInterests.includes(interest.id) ? styles.selected : ''
                            }`}
                        onClick={() => toggleInterest(interest.id)}
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                        style={{ '--accent': interest.color } as React.CSSProperties}
                    >
                        <span className={styles.interestEmoji}>{interest.emoji}</span>
                        <span className={styles.interestLabel}>{interest.label}</span>
                        {selectedInterests.includes(interest.id) && (
                            <motion.div
                                className={styles.checkmark}
                                initial={{ scale: 0 }}
                                animate={{ scale: 1 }}
                            >
                                âœ“
                            </motion.div>
                        )}
                    </motion.button>
                ))}
            </div>
            <div className={styles.selectionCount}>
                {selectedInterests.length}/2 selected
            </div>
        </motion.div>,

        // Step 4: Goals
        <motion.div
            key="goals"
            className={styles.stepContent}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
        >
            <span className={styles.stepEmoji}>ðŸŽ¯</span>
            <h2 className={styles.stepTitle}>What do you want to achieve?</h2>
            <p className={styles.stepDescription}>
                Pick your goals and we'll help you reach them
            </p>
            <div className={styles.goalsGrid}>
                {goals.map((goal) => (
                    <motion.button
                        key={goal.id}
                        className={`${styles.goalButton} ${selectedGoals.includes(goal.id) ? styles.selected : ''
                            }`}
                        onClick={() => toggleGoal(goal.id)}
                        whileHover={{ scale: 1.02 }}
                        whileTap={{ scale: 0.98 }}
                    >
                        <span className={styles.goalEmoji}>{goal.emoji}</span>
                        <div className={styles.goalText}>
                            <span className={styles.goalLabel}>{goal.label}</span>
                            <span className={styles.goalDescription}>{goal.description}</span>
                        </div>
                        {selectedGoals.includes(goal.id) && (
                            <motion.div
                                className={styles.checkmark}
                                initial={{ scale: 0 }}
                                animate={{ scale: 1 }}
                            >
                                âœ“
                            </motion.div>
                        )}
                    </motion.button>
                ))}
            </div>
        </motion.div>,
    ];

    return (
        <AnimatePresence>
            {isOpen && (
                <motion.div
                    className={styles.overlay}
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    exit={{ opacity: 0 }}
                >
                    <motion.div
                        className={styles.modal}
                        initial={{ scale: 0.9, opacity: 0, y: 50 }}
                        animate={{ scale: 1, opacity: 1, y: 0 }}
                        exit={{ scale: 0.9, opacity: 0, y: 50 }}
                        transition={{ type: 'spring', stiffness: 400, damping: 30 }}
                    >
                        {/* Close Button */}
                        <button className={styles.closeButton} onClick={onClose}>
                            <X size={20} />
                        </button>

                        {/* Progress */}
                        <div className={styles.progress}>
                            {[0, 1, 2, 3, 4].map((s) => (
                                <div
                                    key={s}
                                    className={`${styles.progressDot} ${s <= step ? styles.active : ''}`}
                                />
                            ))}
                        </div>

                        {/* Steps */}
                        <AnimatePresence mode="wait">
                            {steps[step]}
                        </AnimatePresence>

                        {/* Navigation */}
                        <div className={styles.navigation}>
                            {step > 0 && (
                                <Button variant="ghost" onClick={handleBack} leftIcon={<ArrowLeft size={18} />}>
                                    Back
                                </Button>
                            )}
                            <Button
                                variant="glow"
                                onClick={handleNext}
                                disabled={!canProceed()}
                                rightIcon={step < 4 ? <ArrowRight size={18} /> : undefined}
                                className={step === 0 ? styles.startButton : ''}
                            >
                                {step === 0 ? "Let's Go!" : step === 4 ? "Start Exploring" : 'Continue'}
                            </Button>
                        </div>
                    </motion.div>
                </motion.div>
            )}
        </AnimatePresence>
    );
};

export default OnboardingModal;
