'use client';

import { motion } from 'framer-motion';
import { Heart, Clock, Star, ChevronRight } from 'lucide-react';
import styles from './OpportunityCard.module.css';

export interface Opportunity {
    id: string;
    title: string;
    organizer: string;
    category: string;
    categoryColor: string;
    deadline: string;
    prize?: string;
    difficulty: 'Beginner' | 'Intermediate' | 'Advanced';
    rating?: number;
    tags: string[];
    isNew?: boolean;
    isHot?: boolean;
    isSaved?: boolean;
}

interface OpportunityCardProps {
    opportunity: Opportunity;
    onSave?: (id: string) => void;
    onClick?: (id: string) => void;
}

const difficultyColors: Record<string, { bg: string; text: string }> = {
    Beginner: { bg: 'rgba(34, 197, 94, 0.15)', text: '#22c55e' },
    Intermediate: { bg: 'rgba(249, 115, 22, 0.15)', text: '#f97316' },
    Advanced: { bg: 'rgba(239, 68, 68, 0.15)', text: '#ef4444' },
};

const OpportunityCard = ({
    opportunity,
    onSave,
    onClick,
}: OpportunityCardProps) => {
    const handleSaveClick = (e: React.MouseEvent) => {
        e.stopPropagation();
        onSave?.(opportunity.id);
    };

    return (
        <motion.article
            className={styles.card}
            whileHover={{ y: -4 }}
            onClick={() => onClick?.(opportunity.id)}
        >
            {/* Header */}
            <div className={styles.header}>
                <div className={styles.badges}>
                    {opportunity.isNew && (
                        <span className={styles.newBadge}>New</span>
                    )}
                    {opportunity.isHot && (
                        <span className={styles.hotBadge}>ðŸ”¥ Hot</span>
                    )}
                </div>
                <motion.button
                    className={`${styles.saveButton} ${opportunity.isSaved ? styles.saved : ''}`}
                    whileHover={{ scale: 1.1 }}
                    whileTap={{ scale: 0.9 }}
                    onClick={handleSaveClick}
                >
                    <Heart size={18} fill={opportunity.isSaved ? 'currentColor' : 'none'} />
                </motion.button>
            </div>

            {/* Content */}
            <div className={styles.content}>
                <span
                    className={styles.category}
                    style={{ color: opportunity.categoryColor }}
                >
                    {opportunity.category}
                </span>
                <h3 className={styles.title}>{opportunity.title}</h3>
                <p className={styles.organizer}>{opportunity.organizer}</p>
            </div>

            {/* Tags */}
            <div className={styles.tags}>
                <span
                    className={styles.difficultyTag}
                    style={{
                        backgroundColor: difficultyColors[opportunity.difficulty].bg,
                        color: difficultyColors[opportunity.difficulty].text,
                    }}
                >
                    {opportunity.difficulty}
                </span>
                {opportunity.tags.slice(0, 2).map((tag) => (
                    <span key={tag} className={styles.tag}>{tag}</span>
                ))}
            </div>

            {/* Footer */}
            <div className={styles.footer}>
                <div className={styles.meta}>
                    {opportunity.prize && (
                        <span className={styles.prize}>Prize: {opportunity.prize}</span>
                    )}
                    <span className={styles.deadline}>
                        <Clock size={14} />
                        {opportunity.deadline}
                    </span>
                </div>
                {opportunity.rating && (
                    <div className={styles.rating}>
                        <Star size={14} fill="#fbbf24" color="#fbbf24" />
                        <span>{opportunity.rating}</span>
                    </div>
                )}
            </div>
        </motion.article>
    );
};

export default OpportunityCard;
