'use client';

import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import Link from 'next/link';
import { Menu, X, Compass, BookOpen, User, Sparkles } from 'lucide-react';
import { XPBar } from '@/components/ui/ProgressBar';
import StreakCounter from '@/components/game/StreakCounter';
import Button from '@/components/ui/Button';
import { useGameStore } from '@/store/gameStore';
import styles from './Header.module.css';

interface HeaderProps {
    onGetStarted?: () => void;
}

const Header = ({ onGetStarted }: HeaderProps) => {
    const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
    const { isOnboarded, xp, xpToNextLevel, level, currentStreak, longestStreak, checkIn, lastCheckIn } = useGameStore();

    // Check if can check in today
    const canCheckIn = !lastCheckIn || new Date(lastCheckIn).toDateString() !== new Date().toDateString();

    const navLinks = [
        { href: '/opportunities', label: 'Explore', icon: Compass },
        { href: '/categories', label: 'Categories', icon: BookOpen },
    ];

    return (
        <header className={styles.header}>
            <div className={styles.container}>
                {/* Logo */}
                <Link href="/" className={styles.logo}>
                    <motion.div
                        className={styles.logoIcon}
                        whileHover={{ rotate: 15, scale: 1.1 }}
                        transition={{ type: 'spring', stiffness: 400, damping: 15 }}
                    >
                        <Sparkles size={24} />
                    </motion.div>
                    <span className={styles.logoText}>Myark</span>
                </Link>

                {/* Desktop Navigation */}
                <nav className={styles.desktopNav}>
                    {navLinks.map((link) => (
                        <Link key={link.href} href={link.href} className={styles.navLink}>
                            <link.icon size={18} />
                            <span>{link.label}</span>
                        </Link>
                    ))}
                </nav>

                {/* Right Section */}
                <div className={styles.rightSection}>
                    {isOnboarded ? (
                        <>
                            {/* Streak */}
                            <div className={styles.streakWrapper}>
                                <StreakCounter
                                    currentStreak={currentStreak}
                                    longestStreak={longestStreak}
                                    variant="compact"
                                />
                            </div>

                            {/* XP Bar */}
                            <div className={styles.xpWrapper}>
                                <XPBar
                                    currentXP={xp}
                                    xpToNextLevel={xpToNextLevel}
                                    level={level}
                                    compact
                                />
                            </div>

                            {/* Profile */}
                            <motion.button
                                className={styles.profileButton}
                                whileHover={{ scale: 1.05 }}
                                whileTap={{ scale: 0.95 }}
                            >
                                <User size={20} />
                            </motion.button>
                        </>
                    ) : (
                        <Button variant="glow" size="sm" onClick={onGetStarted}>
                            Get Started
                        </Button>
                    )}

                    {/* Mobile Menu Toggle */}
                    <button
                        className={styles.mobileMenuToggle}
                        onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
                    >
                        {isMobileMenuOpen ? <X size={24} /> : <Menu size={24} />}
                    </button>
                </div>
            </div>

            {/* Mobile Menu */}
            <AnimatePresence>
                {isMobileMenuOpen && (
                    <motion.div
                        className={styles.mobileMenu}
                        initial={{ opacity: 0, height: 0 }}
                        animate={{ opacity: 1, height: 'auto' }}
                        exit={{ opacity: 0, height: 0 }}
                        transition={{ duration: 0.2 }}
                    >
                        <nav className={styles.mobileNav}>
                            {navLinks.map((link) => (
                                <Link
                                    key={link.href}
                                    href={link.href}
                                    className={styles.mobileNavLink}
                                    onClick={() => setIsMobileMenuOpen(false)}
                                >
                                    <link.icon size={20} />
                                    <span>{link.label}</span>
                                </Link>
                            ))}
                        </nav>

                        {isOnboarded && (
                            <div className={styles.mobileGameStats}>
                                <XPBar
                                    currentXP={xp}
                                    xpToNextLevel={xpToNextLevel}
                                    level={level}
                                />
                                <StreakCounter
                                    currentStreak={currentStreak}
                                    longestStreak={longestStreak}
                                    variant="detailed"
                                    showCheckIn
                                    canCheckIn={canCheckIn}
                                    onCheckIn={checkIn}
                                />
                            </div>
                        )}

                        {!isOnboarded && (
                            <div className={styles.mobileActions}>
                                <Button variant="glow" fullWidth onClick={onGetStarted}>
                                    Get Started Free
                                </Button>
                            </div>
                        )}
                    </motion.div>
                )}
            </AnimatePresence>
        </header>
    );
};

export default Header;
