'use client';

import { motion } from 'framer-motion';
import { Flame, Calendar } from 'lucide-react';
import styles from './StreakCounter.module.css';

interface StreakCounterProps {
    currentStreak: number;
    longestStreak: number;
    variant?: 'default' | 'compact' | 'detailed';
    showCheckIn?: boolean;
    canCheckIn?: boolean;
    onCheckIn?: () => void;
}

const StreakCounter = ({
    currentStreak,
    longestStreak,
    variant = 'default',
    showCheckIn = false,
    canCheckIn = false,
    onCheckIn,
}: StreakCounterProps) => {
    if (variant === 'compact') {
        return (
            <motion.div
                className={styles.compact}
                whileHover={{ scale: 1.05 }}
                transition={{ type: 'spring', stiffness: 400, damping: 25 }}
            >
                <motion.span
                    className={styles.fireEmoji}
                    animate={{ scale: [1, 1.2, 1], rotate: [-5, 5, -5] }}
                    transition={{ repeat: Infinity, duration: 1.5 }}
                >
                    ðŸ”¥
                </motion.span>
                <span className={styles.compactCount}>{currentStreak}</span>
            </motion.div>
        );
    }

    return (
        <div className={`${styles.container} ${styles[variant]}`}>
            <div className={styles.main}>
                <motion.div
                    className={styles.fireContainer}
                    animate={currentStreak > 0 ? {
                        scale: [1, 1.1, 1],
                    } : {}}
                    transition={{ repeat: Infinity, duration: 2 }}
                >
                    <Flame className={`${styles.fireIcon} ${currentStreak > 0 ? styles.active : ''}`} />
                    {currentStreak > 0 && (
                        <div className={styles.fireGlow} />
                    )}
                </motion.div>
                <div className={styles.streakInfo}>
                    <span className={styles.label}>Day Streak</span>
                    <motion.span
                        className={styles.count}
                        key={currentStreak}
                        initial={{ scale: 1.3, opacity: 0 }}
                        animate={{ scale: 1, opacity: 1 }}
                        transition={{ type: 'spring', stiffness: 500, damping: 25 }}
                    >
                        {currentStreak}
                    </motion.span>
                </div>
            </div>

            {variant === 'detailed' && (
                <div className={styles.stats}>
                    <div className={styles.stat}>
                        <Calendar size={14} />
                        <span>Best: {longestStreak} days</span>
                    </div>
                </div>
            )}

            {showCheckIn && (
                <motion.button
                    className={`${styles.checkInButton} ${canCheckIn ? styles.canCheckIn : styles.checkedIn}`}
                    onClick={canCheckIn ? onCheckIn : undefined}
                    whileHover={canCheckIn ? { scale: 1.02 } : {}}
                    whileTap={canCheckIn ? { scale: 0.98 } : {}}
                    disabled={!canCheckIn}
                >
                    {canCheckIn ? (
                        <>
                            <span>ðŸŒŸ</span>
                            <span>Check In Today!</span>
                        </>
                    ) : (
                        <>
                            <span>âœ“</span>
                            <span>Checked In!</span>
                        </>
                    )}
                </motion.button>
            )}
        </div>
    );
};

export default StreakCounter;

// Weekly Streak Calendar
interface WeeklyStreakProps {
    streakDays: boolean[]; // 7 booleans for each day of the week
}

export const WeeklyStreak = ({ streakDays }: WeeklyStreakProps) => {
    const dayLabels = ['M', 'T', 'W', 'T', 'F', 'S', 'S'];

    return (
        <div className={styles.weeklyStreak}>
            {dayLabels.map((label, index) => (
                <div key={index} className={styles.weekDay}>
                    <span className={styles.weekDayLabel}>{label}</span>
                    <motion.div
                        className={`${styles.weekDayDot} ${streakDays[index] ? styles.active : ''}`}
                        initial={streakDays[index] ? { scale: 0 } : {}}
                        animate={streakDays[index] ? { scale: 1 } : {}}
                        transition={{ type: 'spring', stiffness: 500, damping: 25, delay: index * 0.05 }}
                    >
                        {streakDays[index] && <span>ðŸ”¥</span>}
                    </motion.div>
                </div>
            ))}
        </div>
    );
};
